.side-menuv {
    overflow-y: scroll;
   

    .menu-listv {
        ul {


            li {
                display: flex;
                list-style: none;
                padding: 14px 18px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                
              

                svg {
                    margin-left: 10px;
                    font-size: 22px;
                }

                &:hover {
                    background-color: black;
                    color: aqua;
                    cursor: pointer;
                  
                    
                    


                }

            }

        }
    }
}
