@import "../../../css-config/mixins.scss";

   
    .main-parallax{
        height: fit-content;
        background-color: black;
        // position: relative;
        overflow: hidden;
            .para-heading{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 20px;
                height:600px;
                font-size: 58px;
                color: white;
                .first-heading{
                    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
                }
                .second-heading{
                    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
                }
                .third-heading{
                    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
                }
            }
            .ship {
                position: fixed;
                top: 40%;
                left: 30%;
                transform: translate(50%, 50%);
                z-index: 2;
              }
              .ship img {
                transform: rotate(180deg);
                
              }
            // .ship{
            //     margin: auto;
            //     background-image: url('../../../assets/Parallaximages/Rocket.gif');
            //     height: 200px;
            //     width: 200px;
            //     background-size: contain;
            //     background-blend-mode: multiply;
            //     transform: rotate(180deg);
            //     animation: shipAnimation 10s linear infinite;
               
            // }
            .para-content{
               display: flex;
               justify-content: center;
               align-items: center;
              
                .img-desc{
                    height: 200px;
                    width: 400px;
                    color: white;
                }
                
                .para-img{
                   
                    height: 500px;
                    width: 400px;
                    background-image: url("../../../assets/Parallaximages/para1.jpg");
                    background-size: contain;
                    background-repeat: no-repeat;
                }
                
                .para-border{
                    position: relative;
                    margin-top: 5%;
                    left: 30%;
                    height: 500px;
                    width: 400px;
                    border: 3px solid red
                }
                
            }
            .para-content2{
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 100px;
                .para-img2{
                    background-image: url('../../../assets/Parallaximages/para2.jpg');
                    height: 500px;

                    width: 287px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    }
                .para-border2{
                    position: relative;
                    margin-top: 5%;
                    left: -30%;
                    height: 500px;
                    width: 287px;
                    border: 3px solid red
                }
                .img-desc2{
                    height: 200px;
                    width: 400px;
                    color: white;
                  
                }

                
        }
       
        }
        .para-content3{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 100px;
            .para-img3{
                background-image: url('../../../assets/Parallaximages/para3.jpg');
                height: 500px;
                width: 500px;
                background-size: cover;
                background-repeat: no-repeat;
            }
            .para-border3{
                position: relative;
                margin-top: 5%;
                left: -30%;
                height: 500px;
                width: 500px;
                border: 3px solid red
            }
            .img-desc3{
                height: 200px;
                width: 400px;
                color: white;
              
            }
    }
          
    
    