@import "../../css-config/mixins.scss";
.model{
 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  position: fixed;
  
  width: 100%;
  height: 100%;
  

  z-index: 9;
  background-color: white;
  animation: mymove 0.5s ease;
  @include md{
    height: 300px;
  width: 600px;

  }


  
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  .content_pdf{
    
     

    .pdf-top{
   
      padding: 30px 0;
      position: relative;
      
      font-size: 28px;

      .pdf-heading{
        color: blue;
        text-align: center;
     
      }

      .close-pdf{
       
        position: absolute;
        right: 0;
        top: 0;
        padding: 12px;
        cursor: pointer;
        transition: transform .5s ;
        

        &:hover{
          transform: rotate(90deg)
        }
      }

    
      




    }

    .all_pdf{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      display: grid;
  height: 200px;
  justify-content: space-between;
  align-content: center;
  grid-template-columns: auto auto;
 column-gap: 55px;
 row-gap: 32PX;

  // padding: 0 20px;

  @include md{
    column-gap: 5px;
    row-gap: 12PX;
    position: static;
    transform: none;
    grid-template-columns: auto auto auto;
    padding: 70px;
   

  }

  div{
   
    text-align: center;
    padding: 20px 0;
    font-size: 30px;
  }
    }
  }
}

@keyframes mymove {
  0%   {transform: translate(0px,0px);}
  100%  {transform: translate(-50%,-50%);}
  
}

