@import "../../../../css-config/mixins.scss";

.mainh{
    .react-multiple-carousel__arrow {
        position: absolute;
        outline: 0;
        transition: all .5s;
        border-radius: 35px;
        z-index: 1;
        border: 0;
        background: rgba(0, 0, 0, 0.5);
        min-width: 43px;
        min-height: 43px;
        opacity: 1; 
        cursor: pointer
    }


    

 

   
    

 

   
    


     border-bottom: 1px solid rgba(0,0,0,0.5);
    .studenth{
        text-align: center;
        font-size: 32px;
        font-weight: 500;
        padding: 20px 0;
        border-bottom: 1px solid rgba(0,0,0,0.5);
        
    }
   

  .imageh{
    height: fit-content;
    width: fit-content;
    margin: 70px 20px;
    margin-top: 10px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    // padding: 4px 8px;
    display: flex;
    // gap: 10px;
    align-items: center;
    justify-content: center;
    // flex-direction: column;
    cursor: pointer;

    &:hover{
        background-color: black;
        color: white;
        transition: .8s;
    }
   
   
   

    @include md{
        margin-top: 20px;
       
    }

  
   

    .borderh{
