@import '../../../css-config/mixins.scss';

.carouselc {
    height: 787px;
    width: 100%;
    margin: 120px 0px;
    flex-flow: wrap;
    align-items: center;
    display: flex;
    justify-content: center;
    

    gap: 80px;


    @include me {
        display: none;
    }

    @include ml {
        height: 400px;
    }


    @include xl {
        gap: 200px;
    }

    .info-box {
        margin: 0px 20px;
        padding: 20px;
        background-color: #69d6fa;
        border-radius: 10px;
        width: 442px;
        height: fit-content;
        font-size: 18px;
        text-align: justify;




        .heading {
            text-align: center;
            padding-bottom: 20px;
            letter-spacing: 3px;
            line-height: 8px;
            text-transform: capitalize;

           

        }

        .paragraphb {
            color: rgba(0, 0, 0, 0.5);
            text-transform: capitalize;
            line-height: 25px;
        }
    }
}