@import "../../../css-config/mixins.scss";

   
    .main-parallax{
        height: fit-content;
        background-color: black;
        // position: relative;
        overflow: hidden;
        
       
        }
        .para-content3{
            display: flex;
            justify-content: center;
            align-items: center;
         
            padding: 100px;
            .para-img3{
                background-image: url('../../../assets/Parallaximages/para3.jpg');
                height: 500px;
                width: 500px;
                background-size: cover;
                background-repeat: no-repeat;
                animation: Slidein 2s ease-in 2s ;
            }
            .para-border3{
                position: relative;
                margin-top: 5%;
                left: -30%;
                height: 500px;
                width: 500px;
                border: 3px solid red
            }
            .img-desc3{
                height: 200px;
                width: 400px;
                color: white;
              
            }
    }
          
    
    