@import "../../../css-config/mixins.scss";


.box-main{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 40px 0;


.main-chart{
   
  width: 500px;
    height: 480px;
    margin-right: 30px;
    position: relative;
    // background-color: rgb(255,255,255);
    // background-color: #ddcccc;
    overflow-x: auto;
   
    

    .upper-chart{
        display: flex;
        gap: 30px;
        position: absolute;
        top: 30px;
        left: 30px;
       
    }
    .upper-chart1{
        background-color: white;
        height: 60px;
        width: 150px;
        box-shadow: 0 0 0 5px #d5cccc;
        border-radius: 5px;
    }
    .middle-charts{
        display: flex;
        justify-content: center;
        align-items: center;
    //    position: absolute;
    //    top: 140px;
    //    left: 30px;
        gap: 30px;
        padding: 0 30px;
  }
  .chart1{
    height: 40px;
    width: 120px;
    box-shadow: 0 0 0 5px #d5cccc;
        background-color: rgb(0, 123, 255);   
       
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        
  }
  .lower-chart{
    display: flex;
    gap: 30px;
    position: absolute;
    top: 250px;
    left: 30px;
   
}
.lower-chart1{
    background-color: white;
    height: 60px;
    width: 150px;
    box-shadow: 0 0 0 5px #d5cccc;
    border-radius: 5px;
}
.bottom-chart{
    display: flex;
    gap: 30px;
    position: absolute;
    top: 360px;
    left: 30px;
   
}
.bottom-chart1{
    background-color: white;
    height: 60px;
    width: 150px;
    box-shadow: 0 0 0 5px #d5cccc;
    border-radius: 5px;
}
}
}