@import "../../../../../../css-config/mixins.scss";

.hero-banner3{
    padding:40px 0;
    position: relative;
    height: calc(60vh - 20px);
    background-color: rgba(0, 60, 255, 0.3);
    background-image: none;
   
    @include md{
        height: calc(100vh - 80px);
        
        background-image:url("../../../../../../assets/wood.png") ;

    }
   
   
   

    .content3{
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction:column-reverse ;
        max-width: calc(100% - 20px);
        
        margin: 0 auto;
        position: relative;
        @include md{
            flex-direction: row;
            max-width: 1200px;
        }

        .banner-img3{
            position: relative;
            z-index: 9;
            width: 200px;
            margin-bottom: 20px;
            @include md {
                width: 300px;
                margin-right: 50px;
                margin-top: 50px;
                margin-bottom: 0;
            }

            @include xxl {
                width: 600px;
                margin-right: 0px;
                margin-top: 0px;
                
            }

        }

        .text-content3{
            color: white;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            @include md{
                position: absolute;
                left:50px;
                top: 50%;
                transform: translateY(-50%);
            }
            @include xxl{
                left: 0;

            }

            h1{
                font-size: 30px;
                font-weight: 700;
                line-height: 1;
                margin-bottom: 20px;
                @include md{
                    font-size: 60px;
                   
                }
            }
            p{
                max-width: 300px;
                font-size: 14px;
                line-height: 20px;
                margin-bottom: 20px;
                @include md{
                    max-width:500px;
                    font-size: 18px;
                    line-height: 24px;
                    margin-bottom: 40px;
                }
            }

            .ctas3{
                display: flex;
                justify-content: center;
                gap: 20px;

                .banner-cta3{
                    text-transform: uppercase;
                    letter-spacing: 3px;
                    cursor: default;
                    font-size: 19px;
                    font-weight: 500;
                    // width: fit-content;
                    border: 2px solid rgb(0, 0, 0);
                    padding: 10px 20px;
                    
                   
                    &:hover{
                        opacity: 0.6;
                        background-color: black;
                    }
                }

            }
        }

    }


}